.card {
  background-color: hsl(0, 0%, 100%);
  border-radius: 16px;
  width: 324px;
  overflow: hidden;
}
@media (min-width: 40em) {
  .card {
    width: 348px;
  }
}

.profile article:first-child {
  position: relative;
}
.profile img {
  display: block;
}
.profile__image {
  border-radius: 50%;
  position: absolute;
  transform: translateX(-50%);
  left: 50%;
  bottom: -3rem;
  border: 5px solid #fff;
}
.profile article:nth-child(2) {
  border-bottom: 1px solid var(--dark-gray-line);
  padding-bottom: 1.5rem;
}
.profile__name {
  padding-top: 4rem;
  text-align: center;
  color: var(--very-dark-desaturated-blue);
  margin-bottom: 8px;
}
.profile__name span {
  color: var(--dark-grayish-blue);
  font-weight: 400;
}
.profile__country {
  color: var(--dark-grayish-blue);
  font-size: 16px;
  text-align: center;
  margin: 0;
}
.profile__stats {
  padding: 26px 30px;
}
.profile__stats ul {
  display: flex;
  justify-content: space-between;
  margin: 0;
}
.profile__stats li {
  color: var(--very-dark-desaturated-blue);
  font-weight: 700;
  text-align: center;
}
.profile__stats span {
  color: var(--dark-gray);
  display: block;
  font-weight: 400;
  margin-top: 6px;
  font-size: 14px;
  letter-spacing: 0.1rem;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  background-color: var(--dark-cyan);
  font-family: var(--font-kumbh);
  font-size: 18px;
  margin: 0 1rem;
  min-height: calc(100vh - 0.1px);
  padding: 1.5rem;
  background-image: url(../images/bg-pattern-top.svg), url(../images/bg-pattern-bottom.svg);
  background-repeat: no-repeat, no-repeat;
  background-position: right 50vw bottom 34vh, left 50vw top 30vh;
}

.bg-top {
  width: 50%;
  position: absolute;
  left: -600px;
  top: -600px;
  z-index: -1;
}

.bg-bottom {
  position: absolute;
  bottom: -90%;
  right: -70%;
  z-index: -1;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.attribution {
  margin-top: 2rem;
  font-size: 12px;
  text-align: center;
  color: hsl(0, 0%, 100%);
}

.attribution a {
  font-weight: 700;
  color: hsl(229, 23%, 23%);
}

:root {
  --dark-cyan: hsl(185, 75%, 39%);
  --my-dark-cyan: hsla(185, 75%, 39%, 0.3);
  --very-dark-desaturated-blue: hsl(229, 23%, 23%);
  --dark-grayish-blue: hsl(227, 10%, 46%);
  --dark-gray: hsl(0, 0%, 59%);
  --dark-gray-line: hsl(0, 0%, 59%, 0.3);
}

:root {
  --font-kumbh: "Kumbh Sans", sans-serif;
}

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

ul {
  list-style-type: none;
  padding-left: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}